body {
  background: rgb(0, 0, 0);
  background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 1) 0%,
      rgba(25, 24, 30, 1) 60%
    )
    fixed;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  color: #fff;
}
a:hover {
  text-decoration: none;
  color: #fff;
}
.text-muted {
  color: #abaab0 !important;
}
/* ------------------------------------
            Background Color
--------------------------------------*/
.bg-dark {
  background-color: #212026;
}
/* ------------------------------------
                Badge
--------------------------------------*/
.badge-primary {
  background-color: #6182cf;
}
/* ------------------------------------
                Sidebar
--------------------------------------*/
.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 48px 0 0; /* Height of navbar */
  background-color: #19181e;
}

@media (max-width: 767.98px) {
  .sidebar {
    top: 5rem;
  }
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: 0.5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .sidebar-sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

.sidebar .nav-link {
  font-weight: 500;
  color: #fff;
  padding: 0 1rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.sidebar .nav-link:hover,
.sidebar .nav-link.active {
  color: #6182cf;
  border-right: 3px solid #6182cf;
}

.sidebar-heading {
  font-size: 0.75rem;
  text-transform: uppercase;
}
/* ------------------------------------
                  Navbar
--------------------------------------*/
.navbar-brand {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-size: 1rem;
  background-color: #19181e;
}

.navbar-brand a {
  padding: 0.5rem 1rem;
  color: #fff;
}
.navbar-brand a:hover {
  text-decoration: none;
}
.nav-item a {
  font-weight: bold;
  color: #7b7a80;
}
.nav-item a:hover,
.nav-item.active a {
  font-weight: bold;
  color: #6182cf;
}

.navbar .navbar-toggler {
  top: 0.25rem;
  right: 1rem;
}

.custom-small {
  max-height: 16px;
  max-width: 16px;
}
.search-input button,
.search-input input {
  border: none;
  color: #c6c6c8;
  background-color: #1d1d1d;
}
.search-input button {
  border: 2px solid #3e3e40;
  border-right: none;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}
.search-input input {
  border: 2px solid #3e3e40;
  border-left: none;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}
.search-input input:hover,
.search-input input:focus {
  background-color: #1d1d1d;
  color: #fff;
  border: 2px solid #3e3e40;
  border-left: none;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: none;
}
i.fa-bell span {
  padding: 5px;
  margin-top: -2px;
  margin-left: -7px;
  position: absolute;
}
.profilePic {
  background-color: #23262f;
  color: #fff;
  border-radius: 6px;
}
.profilePic img {
  background-color: #33333f;
  max-height: 50px;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
/* ------------------------------------
                  main
--------------------------------------*/
/* .current-playing {
  background-image: url(./images/camila.png);
  background-repeat: no-repeat;
