.card {
  border-radius: 7px;
}
.card,
.card-header {
  border: none;
  background-color: #212026;
  color: #fff;
}
.card-header a:hover {
  text-decoration: none;
  color: #6182cf !important;
}
.top-artists img {
  border-radius: 15px;
}
/* ------------------------------------
              Table Cover
--------------------------------------*/
th img.cover {
  border-radius: 8px;
}
/* ------------------------------------
              Table Buttons
--------------------------------------*/
.btn-outline-primary {
  color: #6182cf;
  border-color: #6182cf;
  border-radius: 8px;
  background-color: #37343f;
}
.btn-outline-primary:hover {
  color: #6182cf;
  border-color: #6182cf;
  border-radius: 8px;
  background-color: transparent;
}
.table td,
.table th {
  padding: 0.3rem;
}
/* ------------------------------------
                Genres
--------------------------------------*/
.genres-col .bg-1,
.genres-col .bg-2,
.genres-col .bg-3,
.genres-col .bg-4,
.genres-col .bg-5,
.genres-col .bg-6 {
  border-radius: 10px;
  height: 70px;
}
.genres-col .bg-1 {
  background-color: #476a88;
}
.genres-col .bg-2 {
  background-color: #a59a7e;
}
.genres-col .bg-3 {
  background-color: #a24c33;
}
.genres-col .bg-4 {
  background-color: #0e3f46;
}
.genres-col .bg-5 {
  background-color: #a67894;
}
.genres-col .bg-6 {
  background-color: #5547a9;
}
/* ------------------------------------
                Player
--------------------------------------*/
.player .cover {
  border-radius: 9px;
  height: 150px;
}
.player .passed {
  width: 70%;
  height: 3px;
  background-color: #fff;
  display: inline-block;
}
.player .notPassed {
  width: 30%;
  height: 3px;
  background-color: #515056;
  display: inline-block;
}
.player .circle {
  border-radius: 50%;
  border: 4px solid #fff;
  background-color: #212026;
  height: 25px;
  width: 25px;
  display: inline-block;
  position: absolute;
  top: -4px;
  right: 28%;
  transform: scale(0.7);
}
.player .card-footer {
  background-color: #5873ff;
}
.player .btn-play {
  padding: 20px;
  border-radius: 18px;
  font-size: 12px;
  color: #5873ff;
  background-color: #fff;
}
.player .lyrics::before {
  position: absolute;
  content: "^";
  margin-top: -15px;
  margin-left: 15px;
}
/* ------------------------------------
                carousel
--------------------------------------*/
.carousel img {
  margin-right: 80px;
}
.carousel .carousel-title {
  position: absolute;
  margin-top: 30px;
  padding-left: 15px;
}
.carousel-caption {
  position: absolute;
  right: unset;
  bottom: unset;
  left: unset;
  z-index: 10;
  margin-top: 70px;
  padding-left: 25px;
  color: #fff;
  text-align: left;
}
.carousel .carousel-caption .carousel-caption-heading {
  font-size: 58px;
}
.carousel .carousel-caption i.fa-heart {
  color: #5871ff;
  border: 2px solid #bcbbc0;
  padding: 10px;
  border-radius: 50%;
}
.btn-primary {
  color: #fff;
  border-color: #5871ff;
  background-color: #5871ff;
  border-radius: 50px;
  padding: 10px 15px;
}
.btn-primary:hover {
  color: #6182cf;
  border-color: #6182cf;
  border-radius: 8px;
  background-color: transparent;
}
.carousel-indicators {
  position: absolute;
  right: 0;
  bottom: 0;
  left: unset;
  z-index: 15;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 0;
  margin-top: 15%;
  margin-right: 5%;
  margin-bottom: 15%;
  list-style: none;
}
.carousel-indicators li {
  height: 5px;
  width: 5px;
  border-radius: 50%;
  margin: 5px 0px;
  border: none;
}
.carousel-control-next,
.carousel-control-prev {
  position: absolute;
  right: 0px;
  left: unset;
  top: unset;
  bottom: 0;
  font-size: 10px;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: unset;
  color: #fff;
  text-align: center;
  opacity: 0.5;
  transition: opacity 0.15s ease;
  margin-top: 15%;
  margin-right: 4.8%;
}
.carousel-control-next {
  margin-bottom: 5%;
}
.carousel-control-prev {
  margin-bottom: 10%;
}
